<template>
  <router-view/>
</template>
<script>
export default{
  mounted() {
    setTimeout(() => {
      console.log('window',window)
      caches.keys().then(function(cacheNames) {
        console.log('Cache storage names:', cacheNames);
      });
      caches.open('backend_faceapi-precache-v2-https//face-sable-nine-vercel.app/ - https://face-sable-nine.vercel.app/').then(function(cache) {
        return cache.keys().then(function(keys) {
          var totalSizeBytes = 0;
          keys.forEach(function(request) {
            cache.match(request).then(function(response) {
              response.blob().then(function(blob) {
                totalSizeBytes += blob.size;
              });
            });
          });
          console.log('Total cache size: ' + totalSizeBytes + ' bytes');
        });
      });
      
    }, 2000);
  },
}
</script>
